"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import Link from "next/link";

const VideosPage = () => {
  const router = useRouter();
  const { id } = router.query;

  const [videos, setVideos] = useState<any>({ promos: [], episodes: [], music_videos: [] });
  const [loading, setLoading] = useState(true);
  const [videoType, setVideoType] = useState<"all" | "episode" | "trailer">("all");
  const [animeInfo, setAnimeInfo] = useState<any>(null);
  const [youtubeTrailers, setYoutubeTrailers] = useState<any[]>([]);
  const [loadingTrailers, setLoadingTrailers] = useState(false);

  // 1. PRIMEIRO: Carregar informa√ß√µes b√°sicas do anime
  useEffect(() => {
    async function loadAnimeInfo() {
      if (!id) return;
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}`);
        const json = await res.json();
        setAnimeInfo(json.data);
      } catch (error) {
        console.error("Erro ao carregar info do anime:", error);
      }
    }
    loadAnimeInfo();
  }, [id]);

  // 2. SEGUNDO: Buscar v√≠deos da API do Jikan
  useEffect(() => {
    async function loadVideos() {
      if (!id) return;
      setLoading(true);
      try {
        const res = await fetch(`https://api.jikan.moe/v4/anime/${id}/videos`);
        const json = await res.json();
        console.log("Dados BRUTOS da API Jikan:", json.data); // DEBUG
        setVideos(json.data || { promos: [], episodes: [], music_videos: [] });
      } catch (error) {
        console.error("Erro ao carregar v√≠deos:", error);
      } finally {
        setLoading(false);
      }
    }
    loadVideos();
  }, [id]);

  // 3. TERCEIRO: Buscar trailers do YouTube (fallback)
  useEffect(() => {
    async function searchYoutubeTrailers() {
      if (!animeInfo?.title) return;
      
      setLoadingTrailers(true);
      try {
        // V√°rias queries poss√≠veis para achar trailers
        const queries = [
          `${animeInfo.title} trailer`,
          `${animeInfo.title} PV`,
          `${animeInfo.title} promotional video`,
          `${animeInfo.title} official trailer`,
          `${animeInfo.title} „Éà„É¨„Éº„É©„Éº`
        ];
        
        // Para cada query, faz uma busca simulada
        const allTrailers: any[] = [];
        
        for (const query of queries) {
          // Esta √© uma URL p√∫blica que n√£o precisa de API key (mas tem limite)
          // Em produ√ß√£o, voc√™ precisaria da API oficial do YouTube
          const searchUrl = `https://www.youtube.com/results?search_query=${encodeURIComponent(query)}&sp=EgIYAQ%253D%253D`;
          
          // Como n√£o podemos fazer scraping do YouTube diretamente no frontend,
          // vamos usar uma abordagem alternativa:
          // 1. Buscar v√≠deos relacionados usando uma API p√∫blica alternativa
          // 2. Ou usar iframe de busca do YouTube
          
          allTrailers.push({
            id: `trailer-${query}`,
            title: `${animeInfo.title} - ${query}`,
            url: `https://www.youtube.com/results?search_query=${encodeURIComponent(query)}`,
            type: "trailer",
            isYouTube: true,
            thumbnail: `https://img.youtube.com/vi/dQw4w9WgXcQ/hqdefault.jpg` // placeholder
          });
        }
        
        setYoutubeTrailers(allTrailers.slice(0, 5)); // Limita a 5 resultados
      } catch (error) {
        console.error("Erro ao buscar trailers:", error);
      } finally {
        setLoadingTrailers(false);
      }
    }
    
    // S√≥ busca trailers se a API do Jikan n√£o retornou promos
    if (animeInfo && videos.promos && videos.promos.length === 0) {
      searchYoutubeTrailers();
    }
  }, [animeInfo, videos.promos]);

  // Juntar todos os v√≠deos
  const jikanVideos = [
    ...(videos.promos || []).map((video: any) => ({ 
      ...video, 
      type: "trailer",
      source: "jikan",
      isYouTube: false
    })),
    ...(videos.episodes || []).map((video: any) => ({ 
      ...video, 
      type: "episode",
      source: "jikan",
      isYouTube: false 
    })),
    ...(videos.music_videos || []).map((video: any) => ({ 
      ...video, 
      type: "music",
      source: "jikan",
      isYouTube: false 
    }))
  ];

  // Adicionar trailers do YouTube se necess√°rio
  const allVideos = [
    ...jikanVideos,
    ...(jikanVideos.filter(v => v.type === "trailer").length === 0 ? youtubeTrailers : [])
  ];

  // Filtrar v√≠deos
  const filteredVideos = allVideos.filter(video => {
    if (videoType === "all") return true;
    if (videoType === "episode") return video.type === "episode";
    if (videoType === "trailer") return video.type === "trailer";
    return true;
  });

  // Extrair ID do YouTube da URL
  const getYouTubeId = (url: string) => {
    if (!url) return null;
    const match = url.match(/(?:youtube\.com\/watch\?v=|youtu\.be\/|youtube\.com\/embed\/)([a-zA-Z0-9_-]+)/);
    return match ? match[1] : null;
  };

  // Gerar thumbnail do YouTube
  const getYouTubeThumbnail = (video: any) => {
    if (video.thumbnail) return video.thumbnail;
    
    const youtubeId = getYouTubeId(video.url || video.video_url);
    if (youtubeId) {
      return `https://img.youtube.com/vi/${youtubeId}/hqdefault.jpg`;
    }
    
    // Placeholder baseado no tipo
    return `https://via.placeholder.com/300x169/${
      video.type === "trailer" ? "FF6B6B" : 
      video.type === "episode" ? "4ECDC4" : 
      "FFD166"
    }/000000?text=${encodeURIComponent(video.type.toUpperCase())}`;
  };

  // DEBUG: Mostrar estrutura dos dados
  useEffect(() => {
    if (!loading && animeInfo) {
      console.log("=== DEBUG VIDEOS ===");
      console.log("Anime:", animeInfo.title);
      console.log("Jikan Promos:", videos.promos?.length || 0);
      console.log("Jikan Episodes:", videos.episodes?.length || 0);
      console.log("YouTube Trailers:", youtubeTrailers.length);
      console.log("All Videos:", allVideos.length);
      console.log("===================");
    }
  }, [loading, animeInfo, videos, youtubeTrailers, allVideos]);

  return (
    <div
      style={{
        width: "100%",
        minHeight: "100vh",
        backgroundImage: "url('/detail.jpg')",
        backgroundSize: "cover",
        backgroundPosition: "center",
        backgroundAttachment: "fixed",
        padding: "20px",
      }}
    >
      <div style={{ maxWidth: "1200px", margin: "0 auto" }}>
        <div style={{ 
          display: "flex", 
          justifyContent: "space-between", 
          alignItems: "center", 
          marginBottom: 30,
          flexWrap: "wrap",
          gap: 15
        }}>
          <h1 style={{ color: "white", margin: 0 }}>
            {animeInfo ? `V√≠deos: ${animeInfo.title}` : "V√≠deos do Anime"}
          </h1>
          
          <div style={{ display: "flex", gap: 15, alignItems: "center" }}>
            <div style={{ display: "flex", gap: 10, flexWrap: "wrap" }}>
              <button
                onClick={() => setVideoType("all")}
                style={{
                  padding: "8px 15px",
                  background: videoType === "all" ? "#1e88e5" : "#333",
                  color: "white",
                  border: "none",
                  borderRadius: "5px",
                  cursor: "pointer",
                  whiteSpace: "nowrap"
                }}
              >
                Todos ({allVideos.length})
              </button>
              <button
                onClick={() => setVideoType("trailer")}
                style={{
                  padding: "8px 15px",
                  background: videoType === "trailer" ? "#1e88e5" : "#333",
                  color: "white",
                  border: "none",
                  borderRadius: "5px",
                  cursor: "pointer",
                  whiteSpace: "nowrap"
                }}
              >
                Trailers ({allVideos.filter(v => v.type === "trailer").length})
              </button>
              <button
                onClick={() => setVideoType("episode")}
                style={{
                  padding: "8px 15px",
                  background: videoType === "episode" ? "#1e88e5" : "#333",
                  color: "white",
                  border: "none",
                  borderRadius: "5px",
                  cursor: "pointer",
                  whiteSpace: "nowrap"
                }}
              >
                Epis√≥dios ({videos.episodes?.length || 0})
              </button>
            </div>
            
            <Link href={`/${id}`}>
              <button style={{ 
                padding: "10px 20px", 
                background: "#333", 
                color: "white", 
                border: "none", 
                borderRadius: "5px", 
                cursor: "pointer",
                fontWeight: "bold",
                whiteSpace: "nowrap"
              }}>
                ‚Ü© Voltar ao Anime
              </button>
            </Link>
          </div>
        </div>

        {/* INFO DEBUG */}
        <div style={{ 
          background: "rgba(0, 0, 0, 0.7)", 
          padding: "15px", 
          borderRadius: "8px",
          marginBottom: "20px",
          color: "white",
          fontSize: "14px"
        }}>
          <p style={{ margin: 0 }}>
            <strong>Status:</strong> {animeInfo?.title || "Carregando..."} | 
            <strong> API Jikan:</strong> {videos.promos?.length || 0} trailer(s), {videos.episodes?.length || 0} epis√≥dio(s) | 
            <strong> Fallback:</strong> {youtubeTrailers.length} trailer(s) do YouTube
          </p>
        </div>

        {/* Aviso se n√£o tem trailers */}
        {!loading && videos.promos?.length === 0 && (
          <div style={{ 
            background: "rgba(255, 152, 0, 0.2)", 
            border: "1px solid #FF9800",
            padding: "15px", 
            borderRadius: "8px",
            marginBottom: "20px",
            color: "#FFCC80",
            fontSize: "14px"
          }}>
            <p style={{ margin: 0 }}>
              ‚ö†Ô∏è <strong>Aten√ß√£o:</strong> A API do Jikan n√£o retornou trailers. 
              {youtubeTrailers.length > 0 
                ? " Mostrando trailers alternativos do YouTube." 
                : " Buscando trailers alternativos..."}
            </p>
          </div>
        )}

        <div style={{ 
          color: "white", 
          marginBottom: 20,
          background: "rgba(0, 0, 0, 0.5)",
          padding: "10px 15px",
          borderRadius: "5px"
        }}>
          <p style={{ margin: 0 }}>
            {filteredVideos.length} v√≠deo{filteredVideos.length !== 1 ? 's' : ''} encontrado{filteredVideos.length !== 1 ? 's' : ''}
            {videoType !== "all" && ` (${videoType === "trailer" ? "Trailers" : "Epis√≥dios"})`}
          </p>
        </div>

        {loading || loadingTrailers ? (
          <p style={{ color: "white", textAlign: "center", fontSize: "18px" }}>Carregando v√≠deos...</p>
        ) : filteredVideos.length === 0 ? (
          <div style={{ 
            textAlign: "center", 
            padding: "40px",
            background: "rgba(0, 0, 0, 0.6)",
            borderRadius: "10px"
          }}>
            <p style={{ color: "white", fontSize: "18px" }}>
              Nenhum v√≠deo encontrado para este anime.
            </p>
          </div>
        ) : (
          <div style={{ 
            display: "grid", 
            gridTemplateColumns: "repeat(auto-fill, minmax(300px, 1fr))", 
            gap: 20
          }}>
            {filteredVideos.map((video: any, index: number) => {
              const url = video.url || video.video_url || "#";
              const isYouTube = video.isYouTube || url.includes("youtube.com") || url.includes("youtu.be");
              const youtubeId = isYouTube ? getYouTubeId(url) : null;
              const thumbnailUrl = getYouTubeThumbnail(video);
              
              return (
                <a
                  key={index}
                  href={url}
                  target="_blank"
                  rel="noopener noreferrer"
                  style={{ 
                    textDecoration: 'none', 
                    color: 'inherit', 
                    display: 'block',
                    background: "rgba(0, 0, 0, 0.8)", 
                    borderRadius: 10, 
                    overflow: "hidden",
                    border: "1px solid rgba(255, 255, 255, 0.1)",
                    transition: "all 0.3s",
                    cursor: "pointer"
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.transform = "translateY(-5px)";
                    e.currentTarget.style.boxShadow = "0 10px 20px rgba(0, 0, 0, 0.5)";
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.transform = "translateY(0)";
                    e.currentTarget.style.boxShadow = "none";
                  }}
                  onClick={(e) => {
                    if (url === "#") {
                      e.preventDefault();
                    }
                  }}
                >
                  {/* THUMBNAIL */}
                  <div style={{ position: "relative", paddingBottom: "56.25%" }}>
                    <img
                      src={thumbnailUrl}
                      alt={video.title}
                      style={{
                        position: "absolute",
                        top: 0,
                        left: 0,
                        width: "100%",
                        height: "100%",
                        objectFit: "cover"
                      }}
                    />
                    
                    {/* BADGE DE ORIGEM */}
                    <div style={{
                      position: "absolute",
                      top: "10px",
                      left: "10px",
                      background: video.source === "jikan" ? "#1e88e5" : "#FF0000",
                      color: "white",
                      padding: "3px 8px",
                      borderRadius: "4px",
                      fontSize: "10px",
                      fontWeight: "bold"
                    }}>
                      {video.source === "jikan" ? "Jikan API" : "YouTube"}
                    </div>
                    
                    {/* BOT√ÉO PLAY */}
                    <div style={{
                      position: "absolute",
                      top: "50%",
                      left: "50%",
                      transform: "translate(-50%, -50%)",
                      background: "rgba(255, 0, 0, 0.8)",
                      width: "50px",
                      height: "50px",
                      borderRadius: "50%",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      fontSize: "20px",
                      color: "white"
                    }}>
                      ‚ñ∂
                    </div>
                  </div>
                  
                  {/* INFORMA√á√ïES */}
                  <div style={{ padding: "15px" }}>
                    <h3 style={{ 
                      color: "white", 
                      margin: "0 0 10px 0",
                      fontSize: "16px",
                      lineHeight: "1.4",
                      height: "3.2em",
                      overflow: "hidden",
                      textOverflow: "ellipsis"
                    }}>
                      {video.title}
                    </h3>
                    
                    <div style={{ 
                      display: "flex", 
                      justifyContent: "space-between",
                      alignItems: "center",
                      marginTop: "10px"
                    }}>
                      <span style={{
                        background: video.type === "trailer" ? "#FF6B6B" : 
                                   video.type === "episode" ? "#4ECDC4" : 
                                   "#FFD166",
                        color: "white",
                        padding: "3px 10px",
                        borderRadius: "12px",
                        fontSize: "12px",
                        fontWeight: "bold"
                      }}>
                        {video.type === "trailer" ? "üé¨ Trailer" : 
                         video.type === "episode" ? "üì∫ Epis√≥dio" : 
                         "üéµ M√∫sica"}
                      </span>
                      
                      <span style={{
                        fontSize: "11px",
                        color: "#aaa",
                        fontStyle: "italic"
                      }}>
                        {video.source === "jikan" ? "MyAnimeList" : "YouTube Search"}
                      </span>
                    </div>
                  </div>
                </a>
              );
            })}
          </div>
        )}

        {filteredVideos.length > 0 && (
          <div style={{ 
            marginTop: "30px", 
            padding: "20px",
            background: "rgba(0, 0, 0, 0.6)",
            borderRadius: "10px",
            textAlign: "center"
          }}>
            <p style={{ color: "#aaa", margin: 0, fontSize: "14px" }}>
              ‚Ä¢ Trailers: {allVideos.filter(v => v.type === "trailer").length} | 
              ‚Ä¢ Epis√≥dios: {allVideos.filter(v => v.type === "episode").length} | 
              ‚Ä¢ M√∫sicas: {allVideos.filter(v => v.type === "music").length}
              <br />
              ‚Ä¢ API Jikan: {jikanVideos.length} v√≠deos | 
              ‚Ä¢ YouTube Fallback: {youtubeTrailers.length} trailers
            </p>
          </div>
        )}
      </div>
    </div>
  );
};

export default VideosPage;